@import '../../../../includes/assets/css/consts.scss';
@import '../base/mixins';

.dpit-theme-minimal {

	// buttons
	--dpit-btn-tx-alpha: 0.8;
	--dpit-btn-tx-alpha-2: 0.85;
	--dpit-btn-tx-alpha-3: 0.85;
	--dpit-btn-tx-color: var(--accent-contrast-inv-color);
	--dpit-btn-tx-on-color: var(--accent-color);
	--dpit-btn-bg-color: transparent;
	--dpit-btn-bg-alpha-2: 1;

	// scrollbar
	--dpit-scrollbar-bg-color: var(--accent-color-gentle);

	// number
	--dpit-number-bg-color: var(--accent-color-gentle);


	// leave default for mobiles
	@include atHoverAbleDev {
		.dpit-scrollbar {
			&::-webkit-scrollbar {
				height: unit(10);
				width: unit(10);
			}
		}
	}

	.dpit-button {
		padding: unit(5) unit(23);
		font-size: unit(38);

		&--no-text:not(.dpit-button--done) {
			font-size: unit(50);

			.dpit-button__ico-wrap {
				&--symbol {
					font-size: unit(50);
				}
			}

			&.dpit-button--skip {
				padding-left: unit(0);
				padding-right: unit(10);
			}
		}

		&--next {
			padding-right: unit(10);
		}

		&--skip {
			padding-left: unit(10);
			padding-right: unit(10);
		}
	}


	.dpit-number {
		&__text {
			font-weight: 700;
		}
	}

	.dpit-tooltip-buttons {
		padding-top: unit(10);
	}

	.dpit-bullets {
		ul {
			&.dpit-has-scrollbar {
				margin-bottom: unit(10);
			}
		}
	}

	.dpit-progress {
		height: unit(12);
	}

}